import React from 'react';

import { checkIsQuestFinished, getQuestDate } from '../utils/date';

type OwnProps = {
	startDate: string;
	endDate: string;
};

export const useQuestTimer = ({ startDate, endDate }: OwnProps) => {
	const timerRef = React.useRef<any>(null);
	const [text, setText] = React.useState('');
	const [isTimer, setIsTimer] = React.useState(false);
	const [isFinished, setIsFinished] = React.useState(false);

	React.useEffect(() => {
		const { isTime, dateText } = getQuestDate(startDate, endDate);

		if (checkIsQuestFinished(endDate)) {
			setIsFinished(true);
		}

		setText(dateText);
		setIsTimer(isTime);
	}, [startDate, endDate]);

	React.useEffect(() => {
		if (isTimer) {
			timerRef.current = setInterval(() => {
				const { dateText } = getQuestDate(startDate, endDate);
				setText(dateText);

				if (checkIsQuestFinished(endDate)) {
					setIsFinished(true);

					if (timerRef.current) {
						clearInterval(timerRef.current);
					}
				}
			}, 1000);
		}
		return () => {
			clearInterval(timerRef.current);
		};
	}, [isTimer, startDate, endDate]);
	return { text, isTimer, isFinished };
};
