import React from 'react';
import { AwardIcon } from 'shared/components/award-icon';
import { AwardType } from 'shared/components/quest/mock';
import { formattedNumber } from 'shared/utils/formattedNumber';

import * as Styled from './quest-awards.styled';

type OwnProps = {
	awards: AwardType[];
	showFull?: boolean;
};

const QuestAwards: React.FC<OwnProps> = ({ awards, showFull }) => {
	const isLabelsShown = awards.length < 3;

	return (
		<>
			{!isLabelsShown && !showFull ? <Styled.Title>Награды:</Styled.Title> : null}
			<Styled.Wrapper $isLabelsShown={isLabelsShown && !showFull} $showFull={showFull}>
				{awards.map(award => (
					<Styled.Item key={award.id}>
						<Styled.Icon>
							<AwardIcon iconTag={award.iconTag} />
						</Styled.Icon>
						{showFull ? <Styled.Label>{award.label}</Styled.Label> : null}
						{showFull || isLabelsShown ? <Styled.Value>{formattedNumber(award.value)}</Styled.Value> : null}
					</Styled.Item>
				))}
			</Styled.Wrapper>
		</>
	);
};

export { QuestAwards };
