import dayjs from 'dayjs';
import duration from 'dayjs/plugin/duration';

dayjs.extend(duration);

const isLessThanADayLeft = (date: Date) => {
	return dayjs(date).diff(dayjs(), 'hour') < 24;
};

const getTimeDifferenceFromNow = (date: Date | string) => {
	return dayjs(date).diff(dayjs());
};

export const checkIsQuestFinished = (date: string | Date) => {
	return dayjs(date).isBefore(dayjs());
};

const formatDate = (date: Date) => {
	return dayjs(date).format('DD.MM');
};

const formatTime = (date: Date | string) => {
	const diffMilliseconds = getTimeDifferenceFromNow(date);
	const diffDuration = dayjs.duration(diffMilliseconds);

	const hours = diffDuration.hours().toString().padStart(2, '0');
	const minutes = diffDuration.minutes().toString().padStart(2, '0');
	const seconds = diffDuration.seconds().toString().padStart(2, '0');

	return `${hours}:${minutes}:${seconds}`;
};

export const getQuestDate = (startDate: string, endDate: string) => {
	const start = new Date(startDate);
	const end = new Date(endDate);
	const showTime = isLessThanADayLeft(end);
	const isFinished = checkIsQuestFinished(end);

	if (showTime && !isFinished) {
		return {
			isTime: showTime,
			dateText: formatTime(end)
		};
	}
	return {
		isTime: showTime && !isFinished,
		dateText: formatDate(start) + '-' + formatDate(end)
	};
};
