import * as React from 'react';

import { ChevronIcon } from '../icons';
import * as Styled from './button.styled';
import { BUTTON_SIZE, ICON_POSITION } from './types';

export type ButtonProps = {
	hasIcon?: boolean;
	color?: Styled.ButtonElementProps['$color'];
	size?: Styled.ButtonElementProps['$size'];
	variant?: Styled.ButtonElementProps['$variant'];
	iconPosition?: Styled.ButtonElementProps['$iconPosition'];
	fluid?: boolean;
	onClick?: React.MouseEventHandler;
	disabled?: boolean;
	tabIndex?: number;
	className?: string;
};

type OwnProps = ButtonProps;

const ButtonComponent: React.ForwardRefRenderFunction<HTMLButtonElement, React.PropsWithChildren<OwnProps>> = (
	{
		hasIcon,
		iconPosition = 'start',
		children,
		color = 'system',
		size = BUTTON_SIZE.MEDIUM,
		variant,
		fluid,
		onClick,
		disabled,
		tabIndex,
		className
	},
	ref
) => (
	<Styled.ButtonElement
		disabled={disabled}
		onClick={onClick}
		tabIndex={tabIndex}
		$hasText={!!children}
		$hasIcon={hasIcon}
		ref={ref}
		$color={color}
		$size={size}
		$variant={variant}
		$fluid={fluid}
		className={className}
	>
		{hasIcon && iconPosition === ICON_POSITION.START && (
			<Styled.IconWrapper>
				<ChevronIcon />
			</Styled.IconWrapper>
		)}

		{children && <Styled.TextWrapper>{children}</Styled.TextWrapper>}

		{hasIcon && iconPosition === ICON_POSITION.END && (
			<Styled.IconWrapper>
				<ChevronIcon.Right />
			</Styled.IconWrapper>
		)}
	</Styled.ButtonElement>
);

export const Button = React.forwardRef(ButtonComponent);
