import React from 'react';
import { CalendarIcon, TimerIcon } from 'shared/components/icons';

import * as Styled from './quest-date.styled';

type OwnProps = {
	text: string;
	isTimer: boolean;
};

const QuestDate: React.FC<OwnProps> = ({ text, isTimer }) => {
	return (
		<Styled.Date>
			{isTimer ? <TimerIcon /> : <CalendarIcon />}
			<Styled.DateText>{text}</Styled.DateText>
		</Styled.Date>
	);
};

export { QuestDate };
