import React from 'react';
import { createPortal } from 'react-dom';

import CloseIcon from '../icons/close-icon';
import { QuestLabel } from '../quest/components/quest-label';
import { QuestLabelIconTag, QuestProgressStatus } from '../quest/types';
import * as Styled from './modal.styled';

export enum MODAL_SIZE {
	MEDIUM = 'medium',
	LARGE = 'large'
}

type OwnProps = {
	hasOverlay?: boolean;
	isOpen: boolean;
	closeModal: () => void;
	icon?: React.ReactNode;
	hasCloseButton?: boolean;
	children?: React.ReactNode;
	title?: string | React.ReactNode;
	size?: MODAL_SIZE;
	tags?: QuestLabelIconTag;
	progress?: number;
};

const Modal: React.FC<OwnProps> = ({
	children,
	hasOverlay = true,
	hasCloseButton = true,
	title,
	isOpen,
	closeModal,
	size = MODAL_SIZE.MEDIUM,
	tags,
	progress
}) => {
	const modalRoot = document.getElementById('modal-root');
	const questProgress =
		progress !== undefined &&
		(progress > 0 && progress < 100
			? QuestProgressStatus.IN_PROGRESS
			: progress === 100
				? QuestProgressStatus.DONE
				: QuestProgressStatus.NOT_STARTED);

	const setModalClosed = (e: React.MouseEvent<HTMLDivElement>) => {
		e.stopPropagation();

		closeModal();
	};

	if (!isOpen || !modalRoot) {
		return null;
	}

	return (
		<>
			<Styled.Overlay onClick={setModalClosed} $hasOverlay={hasOverlay} />
			{createPortal(
				<Styled.ModalWrapper $size={size}>
					{hasCloseButton ? (
						<Styled.CloseButton onClick={setModalClosed}>
							<Styled.IconWrapper>
								<CloseIcon />
							</Styled.IconWrapper>
						</Styled.CloseButton>
					) : null}
					<Styled.ModalHeader>
						{title}
						{!!questProgress ? (
							<Styled.QuestTags>
								<QuestLabel status={questProgress} />
							</Styled.QuestTags>
						) : null}
					</Styled.ModalHeader>
					<Styled.ModalContent>{children}</Styled.ModalContent>
				</Styled.ModalWrapper>,
				modalRoot
			)}
		</>
	);
};

export { Modal };
