import React from 'react';
import ChipsImg from 'shared/assets/chips.png';
import DiamondImg from 'shared/assets/diamond.png';
import FreebetImg from 'shared/assets/freebet_icon.png';
import MoneyImg from 'shared/assets/money.png';
import TrophyImg from 'shared/assets/trophy.png';
import styled from 'styled-components';

export enum IconTag {
	FREEBET = 'freebet',
	MONEY = 'money',
	DIAMOND = 'diamond',
	CHIPS = 'chips',
	TROPHY = 'trophy'
}

export const Icon = styled.img`
	width: 18px;
	height: 18px;
`;

type OwnProps = {
	iconTag: IconTag;
	className?: string;
};

const AwardIcon: React.FC<OwnProps> = ({ iconTag, className }) => {
	return (
		<>
			{iconTag === IconTag.FREEBET ? (
				<Icon src={FreebetImg} className={className} />
			) : iconTag === IconTag.MONEY ? (
				<Icon src={MoneyImg} className={className} />
			) : iconTag === IconTag.DIAMOND ? (
				<Icon src={DiamondImg} className={className} />
			) : iconTag === IconTag.CHIPS ? (
				<Icon src={ChipsImg} className={className} />
			) : (
				<Icon src={TrophyImg} className={className} />
			)}
		</>
	);
};

export default AwardIcon;
