import React from 'react';
import { Button } from 'shared/components/button';
import { QuestAwards } from 'shared/components/quest/components/quest-awards';
import { QuestLabel } from 'shared/components/quest/components/quest-label';
import { QuestThumbnail } from 'shared/components/quest/components/quest-thumbnail';
import { QuestType } from 'shared/components/quest/mock';
import * as Styled from 'shared/components/quest/quest.styled';
import { useQuestTimer } from 'shared/hooks/useQuestTimer';

import { QuestDate } from '../quest-date';
import { NavLink } from 'react-router-dom';

type OwnProps = {
	quest: QuestType;
};

const QuestCategoryCard: React.FC<OwnProps> = ({ quest }) => {
	const { isTimer, text, isFinished } = useQuestTimer({ startDate: quest.startDate, endDate: quest.endDate });
	const hasThumbnail = !!quest.thumbnail && !quest.backgroundImage;

	return (
		<Styled.Wrapper $color={quest.backgroundColor}>
			{isFinished ? <Styled.Overlay /> : null}
			{quest.backgroundImage ? <Styled.BackgroundImage src={quest.backgroundImage} /> : null}
			<Styled.QuestLabelWrapper>
				<QuestLabel iconTag={quest.label} />
			</Styled.QuestLabelWrapper>
			<Styled.Content>
				<Styled.Head>{!hasThumbnail ? <Styled.Progress>{quest.progress}</Styled.Progress> : null}</Styled.Head>
				<Styled.Body>
					{hasThumbnail ? <QuestThumbnail img={quest.thumbnail} /> : null}
					<Styled.Column>
						<Styled.TitleWrapper>
							<Styled.Title $hasThumbnail={hasThumbnail}>{quest.title}</Styled.Title>
						</Styled.TitleWrapper>
						<QuestAwards awards={quest.awards} />
					</Styled.Column>
				</Styled.Body>
				<Styled.Footer>
					<QuestDate text={text} isTimer={isTimer} />
					{hasThumbnail ? <Styled.Progress>{quest.progress}</Styled.Progress> : null}
				</Styled.Footer>
			</Styled.Content>

			<Styled.ActionButton>
				{isFinished ? (
					<Styled.FinishedText>Квест завершен</Styled.FinishedText>
				) : (
					<NavLink to='/quests'>
						<Button hasIcon iconPosition='end'>
							{hasThumbnail && 'Начать'}
						</Button>
					</NavLink>
				)}
			</Styled.ActionButton>
		</Styled.Wrapper>
	);
};

export { QuestCategoryCard };
