import React from 'react';

import * as Styled from './progress-bar.styled';

type OwnProps = {
	progress: number;
	showInfo?: boolean
};

const ProgressBar: React.FC<OwnProps> = ({ progress = 0, showInfo = true }) => {
	return (
		<Styled.Wrapper>
			{
				showInfo && <Styled.Percents>{progress}%</Styled.Percents>
			}
			<Styled.Bar $progress={progress}></Styled.Bar>
		</Styled.Wrapper>
	);
};

export { ProgressBar };
