import React from 'react';
import { ActionButtonIcon } from 'shared/components/icons';
import { formattedNumber } from 'shared/utils/formattedNumber';

import { AwardIcon } from '../award-icon';
import { IconTag } from '../award-icon/award-icon';
import * as Styled from './award.styled';
import { AwardModal } from './components/award-modal';

type OwnProps = {
	type: IconTag;
	prise: string;
	description: string;
};

const Award: React.FC<OwnProps> = ({ type, prise, description }) => {
	const [isOpen, setIsOpen] = React.useState(false);
	const formattedPrice = formattedNumber(prise);

	const closeModal = () => {
		setIsOpen(false);
	};

	return (
		<Styled.Wrapper>
			<Styled.ActionIcon onClick={() => setIsOpen(true)}>
				<ActionButtonIcon />
			</Styled.ActionIcon>
			<Styled.Header>
				<AwardIcon iconTag={type} />
				<Styled.HeaderText>Freebet</Styled.HeaderText>
			</Styled.Header>
			<Styled.Footer>{formattedPrice}тг</Styled.Footer>
			{isOpen ? <AwardModal description={description} isOpen={isOpen} closeModal={closeModal} /> : null}
		</Styled.Wrapper>
	);
};

export { Award };
