import React from 'react';

import * as Styled from './quest-thumbnail.styled';

type OwnProps = {
	img: string;
	isBackgroundColored?: boolean;
};

const QuestThumbnail: React.FC<OwnProps> = ({ img, isBackgroundColored }) => {
	return (
		<Styled.Thumbnail $isBackgroundColored={isBackgroundColored}>
			<Styled.Image src={img} />
		</Styled.Thumbnail>
	);
};

export { QuestThumbnail };
