import React from 'react';
import { Button } from 'shared/components/button';
import { BUTTON_COLOR } from 'shared/components/button/types';
import { ProgressBar } from 'shared/components/progress-bar';
import { QuestAwards } from 'shared/components/quest/components/quest-awards';
import { QuestLabel } from 'shared/components/quest/components/quest-label';
import { QuestThumbnail } from 'shared/components/quest/components/quest-thumbnail';
import { QuestType } from 'shared/components/quest/mock';
import * as Styled from 'shared/components/quest/quest.styled';
import { useQuestTimer } from 'shared/hooks/useQuestTimer';

import { QuestCardDescription } from '../quest-card-description';
import { QuestDate } from '../quest-date';
import { QuestModal } from '../quest-modal';

type OwnProps = {
	quest: QuestType;
};

const QuestCard: React.FC<OwnProps> = ({ quest }) => {
	const [isOpen, setIsOpen] = React.useState(false);
	const { isTimer, text, isFinished } = useQuestTimer({ startDate: quest.startDate, endDate: quest.endDate });

	const buttonStyle = {
		text:
			quest.questProgress > 0 && quest.questProgress < 100
				? 'Играть'
				: quest.questProgress === 0
					? 'Начать'
					: 'К награде',
		color:
			quest.questProgress > 0 && quest.questProgress < 100
				? BUTTON_COLOR.SYSTEM
				: quest.questProgress === 0
					? BUTTON_COLOR.PRIMARY
					: BUTTON_COLOR.SECONDARY
	};

	const setModalVisibility = (state: boolean) => {
		setIsOpen(state);
	};

	return (
		<>
			<Styled.Wrapper $isBig>
				{isFinished ? <Styled.Overlay /> : null}
				<Styled.QuestLabelWrapper>
					<QuestLabel iconTag={quest.label} />
				</Styled.QuestLabelWrapper>
				<Styled.Content>
					<Styled.Body>
						<QuestThumbnail img={quest.thumbnail} isBackgroundColored />
						<Styled.Column>
							<Styled.TitleWrapper>
								<Styled.Title $hasThumbnail={true}>{quest.title}</Styled.Title>
							</Styled.TitleWrapper>
							<QuestAwards awards={quest.awards} />
						</Styled.Column>
					</Styled.Body>
					<QuestCardDescription
						description={quest.description}
						hasProgress={!!quest.questProgress}
						onShowMoreHandler={() => setModalVisibility(true)}
					/>
					<Styled.Footer>
						<QuestDate text={text} isTimer={isTimer} />
						{!isFinished && quest.questProgress > 0 ? (
							<Styled.ProgressWrapper>
								<ProgressBar progress={quest.questProgress} />
							</Styled.ProgressWrapper>
						) : null}
					</Styled.Footer>
				</Styled.Content>
				<Styled.ActionButton>
					{isFinished ? (
						<Styled.FinishedText>Квест завершен</Styled.FinishedText>
					) : (
						<Button color={buttonStyle.color}>{buttonStyle.text}</Button>
					)}
				</Styled.ActionButton>
				<QuestModal quest={quest} isOpen={isOpen} closeModal={() => setModalVisibility(false)} />
			</Styled.Wrapper>
		</>
	);
};

export { QuestCard };
