import React from 'react';
import { Button } from 'shared/components/button';
import { BUTTON_COLOR } from 'shared/components/button/types';
import { Modal } from 'shared/components/modal';
import { ProgressBar } from 'shared/components/progress-bar';

import { QuestType } from '../../mock';
import { QuestAwards } from '../quest-awards';
import * as Styled from './quest-modal.styled';

type OwnProps = {
	isOpen: boolean;
	closeModal: () => void;
	quest: QuestType;
};

const QuestModal: React.FC<OwnProps> = ({ isOpen, closeModal, quest }) => {
	return (
		<Modal
			title={quest.title}
			isOpen={isOpen}
			closeModal={closeModal}
			progress={quest.questProgress}
			tags={quest.label}
		>
			<Styled.Wrapper>
				<Styled.Description>{quest.description}</Styled.Description>
				<Styled.ProgressWrapper>
					<Styled.ProgressTitle>Прогресс:</Styled.ProgressTitle>
					<ProgressBar progress={quest.questProgress} />
				</Styled.ProgressWrapper>
				<Styled.AwardsTitle>Награда:</Styled.AwardsTitle>
				<Styled.AwardsWrapper>
					<QuestAwards awards={quest.awards} showFull />
				</Styled.AwardsWrapper>
				<Styled.ActionsWrapper>
					<Button color={BUTTON_COLOR.PRIMARY} onClick={closeModal}>
						Действие
					</Button>
				</Styled.ActionsWrapper>
			</Styled.Wrapper>
		</Modal>
	);
};

export { QuestModal };
