import React from 'react';

import { mockQuests } from '../quest/mock';
import { QuestItem } from './components/quest-item';
import * as Styled from './quest-map.styled';

const QuestMap = () => {
	const questWithContinueLabel = mockQuests.find(quest => quest.questProgress < 100);

	return (
		<Styled.Wrapper>
			<Styled.Road>
				{mockQuests.map((quest, index) => (
					<QuestItem
						key={quest.id}
						index={index}
						quest={quest}
						quantity={mockQuests.length}
						hasLabel={questWithContinueLabel?.id === quest.id}
					/>
				))}
			</Styled.Road>
		</Styled.Wrapper>
	);
};

export default QuestMap;
