import dayjs from 'dayjs';
import React from 'react';
import { CheckIcon, GlareIcon, LineIcon } from 'shared/components/icons';
import { QuestModal } from 'shared/components/quest/components/quest-modal';
import { QuestType } from 'shared/components/quest/mock';

import { StartLabel } from '../start-label';
import * as Styled from './quest-item.styled';
import { COLORS, POSITIONS } from './quest-item.styled';

type OwnProps = {
	index: number;
	quest: QuestType;
	quantity: number;
	hasLabel?: boolean;
};

const getLineComponent = (index: number) => {
	if (index === 1) {
		return (
			<Styled.Line $position={POSITIONS.BOTTOM}>
				<LineIcon />
			</Styled.Line>
		);
	} else if (index % 4 === 2) {
		return (
			<Styled.Line $position={POSITIONS.BOTTOM_RIGHT}>
				<LineIcon />
			</Styled.Line>
		);
	} else if (index % 4 === 3) {
		return (
			<Styled.Line $position={POSITIONS.RIGHT}>
				<LineIcon />
			</Styled.Line>
		);
	} else if (index % 4 === 0) {
		return (
			<Styled.Line $position={POSITIONS.BOTTOM}>
				<LineIcon />
			</Styled.Line>
		);
	} else if (index % 4 === 1) {
		return (
			<Styled.Line $position={POSITIONS.LEFT}>
				<LineIcon />
			</Styled.Line>
		);
	} else {
		return null;
	}
};

const QuestItem: React.FC<OwnProps> = ({ index, quest, quantity, hasLabel }) => {
	const isEnded = dayjs(quest.endDate) < dayjs();
	const [isOpen, setIsOpen] = React.useState(false);
	return (
		<>
			<Styled.QuestItem
				$color={COLORS.YELLOW}
				$isActive={quest.questProgress > 0 && quest.questProgress < 100}
				$isFinished={quest.questProgress === 100 || isEnded}
				onClick={() => setIsOpen(true)}
			>
				{hasLabel ? <StartLabel /> : null}
				<Styled.Glare>
					<GlareIcon />
				</Styled.Glare>
				{quest.questProgress === 100 || isEnded ? (
					<Styled.CheckWrapper>
						<CheckIcon />
						<CheckIcon />
					</Styled.CheckWrapper>
				) : (
					index + 1
				)}
				{index + 1 < quantity ? getLineComponent(index + 1) : null}
			</Styled.QuestItem>
			<QuestModal quest={quest} isOpen={isOpen} closeModal={() => setIsOpen(false)} />
		</>
	);
};

export { QuestItem };
