import { convertHexToRgbA } from 'shared/styled-system';
import styled, { css } from 'styled-components';

export enum LABEL_COLOR {
	NOT_STARTED = 'not_started',
	DONE = 'done',
	IN_PROGRESS = 'in_progress'
}

type TagProps = {
	$color: Lowercase<keyof typeof LABEL_COLOR>;
};

const colorMixin = (props: TagProps) => {
	if (props.$color === LABEL_COLOR.IN_PROGRESS) {
		return css`
			background-color: ${({ theme }) => convertHexToRgbA(theme.colors.primary100, 0.2)};

			${Text} {
				color: ${({ theme }) => theme.colors.primary100};
			}
		`;
	}
	if (props.$color === LABEL_COLOR.DONE) {
		return css`
			background-color: ${({ theme }) => convertHexToRgbA(theme.colors.system300, 0.2)};

			${Text} {
				color: ${({ theme }) => theme.colors.system300};
			}
		`;
	}
	if (props.$color === LABEL_COLOR.NOT_STARTED) {
		return css`
			background-color: ${({ theme }) => convertHexToRgbA(theme.colors.textNeutral100, 0.2)};

			${Text} {
				color: ${({ theme }) => theme.colors.textNeutral};
			}
		`;
	}
	return css``;
};

export const LabelWrapper = styled.div<TagProps>`
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 4px 6px;
	border-radius: 12px;

	${colorMixin};

	& > svg {
		width: 14px;
		height: 14px;
		fill: ${({ theme }) => theme.colors.textNeutral};
		margin-right: 4px;
	}
`;

export const Text = styled.span`
	font-size: 12px;
	font-weight: 600;
`;
