import React from 'react';

import * as Styled from './quest-card-description.styled';

type OwnProps = {
	description: string;
	hasProgress: boolean;
	onShowMoreHandler: () => void;
};

const QuestCardDescription: React.FC<OwnProps> = ({ description, hasProgress, onShowMoreHandler }) => {
	const containerRef = React.useRef(null);
	const [isOverflowing, setIsOverflowing] = React.useState(false);

	React.useEffect(() => {
		const checkOverflow = () => {
			if (containerRef.current) {
				const { scrollHeight, clientHeight } = containerRef.current;
				setIsOverflowing(scrollHeight > clientHeight);
			}
		};

		checkOverflow();
		window.addEventListener('resize', checkOverflow);
		return () => window.removeEventListener('resize', checkOverflow);
	}, []);

	return (
		<Styled.Description>
			<Styled.DescriptionText ref={containerRef} $hasProgress={hasProgress}>
				{description}
			</Styled.DescriptionText>
			{isOverflowing ? (
				<Styled.DescriptionButton onClick={onShowMoreHandler}>Подробнее</Styled.DescriptionButton>
			) : null}
		</Styled.Description>
	);
};

export { QuestCardDescription };
