import React from 'react';

import { QuestLabelIconTag, QuestProgressStatus, iconMapping, statusMapping } from '../../types';
import * as Styled from './quest-label.styled';

type OwnProps = {
	iconTag?: QuestLabelIconTag;
	status?: QuestProgressStatus;
};

const QuestLabel: React.FC<OwnProps> = ({ iconTag, status }) => {
	const { icon: Icon, text } = iconMapping[iconTag || QuestLabelIconTag.CHIP];
	const { color, text: statusText } = statusMapping[status || QuestProgressStatus.NOT_STARTED];
	const isStatus = !!status;

	return (
		<Styled.LabelWrapper $color={isStatus ? color : Styled.LABEL_COLOR.NOT_STARTED}>
			{!isStatus && Icon ? <Icon /> : null}
			<Styled.Text>{isStatus ? statusText : text}</Styled.Text>
		</Styled.LabelWrapper>
	);
};

export { QuestLabel };
