import React from 'react';

import { QuestCard } from './components/quest-card';
import { QuestCategoryCard } from './components/quest-category-card';
import { QuestType } from './mock';

type OwnProps = {
	isCategory?: boolean;
	quest?: QuestType;
};

const Quest: React.FC<OwnProps> = ({ isCategory, quest }) => {
	if (!quest) {
		return null;
	}

	return isCategory ? <QuestCategoryCard quest={quest} /> : <QuestCard quest={quest} />;
};

export { Quest };
